<template>
  <div class="projCard">
    <div class="projectName">{{projectInfo.name}}</div>
    <div class="projectLang">{{projectInfo.lang.join(', ')}}</div>
    <div class="projectBody">
      <div class="projectDescription">{{projectInfo.description}}</div>
    </div>
    <div class="projectDate">{{projectInfo.date}}</div>
    <div
      class="code Icon"
      v-if="projectInfo.github"
      @click="clickedIcon(projectInfo.github)"
      title="View Code"
    >
      <img src="@/assets/media/GitHubIcon.png" />
    </div>
    <div
      class="pdf Icon"
      v-if="projectInfo.pdf"
      @click="clickedIcon(projectInfo.pdf)"
      title="View Paper"
    >
      <img src="https://img.icons8.com/ios-filled/50/000000/pdf-2--v2.png" />
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectCard",
  props: ["projectInfo"],
  methods: {
    clickedIcon(link) {
      window.open(link);
    }
  }
};
</script>

<style lang="scss">
.projCard {
  width: 100%;
  background: #e2e1e0;
  border-radius: 10px;
  color: #2e3436;
  padding: 1%;
  opacity: 0;
  animation: projectCardLoad cubic-bezier(0.39, 0.575, 0.565, 1) 1s;
  animation-fill-mode: forwards;
  margin-bottom: 2%;
}

@keyframes projectCardLoad {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.projectName {
  font-size: 2rem;
}

.projectBody {
  display: flex;
}

.projectLang {
  font-weight: bold;
}

.projectDescription {
  font-size: 1rem;
}

.Icon {
  float: right;
  & img {
    width: 1.5rem;
  }
  &:hover {
    cursor: pointer;
  }
  pointer-events: all;
  margin: 0px 3px;
}

.projectIcon {
  width: 10%;
  height: 10%;
  border-radius: 4px;
  margin-right: 10px;
  align-self: center;
}

.projectDate {
  font-weight: bold;
  float: left;
  text-indent: 0.5rem;
}
</style>
