<template>
  <simplebar class="projects" ref="projects" data-simplebar-auto-hide="false">
    <FadeIn></FadeIn>
    <div class="projectContainer">
      <div class="backArrow" @click="goHome">
        <img src="https://img.icons8.com/ios-filled/50/000000/left.png" />
      </div>
      <ProjectCard v-for="project in projects" :key="project.name" :projectInfo="project"></ProjectCard>
    </div>
  </simplebar>
</template>

<script>
import FadeIn from "@/components/FadeIn.vue";
import ProjectCard from "@/components/ProjectCard.vue";
import projects from "../assets/projects/projects.json";
import simplebar from "simplebar-vue";
import "simplebar/dist/simplebar.min.css";

export default {
  name: "Projects",
  components: { FadeIn, ProjectCard, simplebar },
  props: ["value"],
  computed: {
    projects() {
      return projects;
    }
  },
  $route(to, from) {
    this.$el.classList.add("noFade");
  },
  methods: {
    goHome() {
      this.$el.classList.add("fadeOut");
      setTimeout(() => {
        this.$router.push("/");
      }, 450);
    }
  },
  mounted() {
    document.querySelector("#background").style.filter = "contrast(2)";
  }
};
</script>

<style lang="scss">
.projects {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.fadeOut::before {
  background: #300a24;
  opacity: 0;
  position: fixed;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  animation: fadeoutanimation 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  z-index: 1000;
  content: "";
}

@keyframes fadeoutanimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.projectContainer {
  width: 70%;
  margin: 5% auto;
  background: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: visible;
  height: max-content;
}

@for $i from 1 through 10 {
  .projectContainer .projCard:nth-child(#{$i}n) {
    animation-delay: #{($i + 1) * 0.25}s;
  }
}

.backArrow {
  align-self: baseline;
  display: inline-block;
  margin-left: 0px;
  transition: margin cubic-bezier(0.445, 0.05, 0.55, 0.95) 0.5s;
  &:hover {
    margin-left: -1%;
    cursor: pointer;
  }
  & img {
    filter: invert(1);
  }
}
</style>
