<template>
  <div class="cardContainer" @click="clicked">
    <div class="card">
      <div class="cardImage">
        <slot></slot>
      </div>
      <div class="cardTextbg svgBlur"></div>
      <div class="cardText">{{ label }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "EventCard",
  props: ["label", "index"],
  mounted() {
    this.$el.setAttribute("style", `--offset: ${this.index * 3}!important;`);
  },
  methods: {
    clicked(event) {
      this.$emit("click", event, this);
    }
  }
};
</script>

<style lang="scss">
.card {
  background: #300a24;
  position: relative;
  display: flex;
  width: 100%;
  flex-direction: column;
  transition: all 0.5s;
  filter: brightness(1);
  &:hover .cardImage {
    filter: brightness(0.9);
  }
}

.cardImage {
  display: flex;
  & img {
    position: relative;
    width: 100%;
    height: auto;
  }
  pointer-events: none;
}

.cardText {
  height: 2rem;
  min-width: 100%;
  text-align: center;
  font-size: 1.5rem;
  color: #fff;
  font-family: Source Code Pro, monospace;
  pointer-events: none;
  z-index: 11;
}

.cardTextbg {
  position: absolute;
  width: 100%;
  background: url("../assets/media/texture2.jpg");
  background-size: 100% auto;
  height: 2rem;
  bottom: 0;
  left: 0;
  z-index: 10;
  background-position-y: calc(var(--offset) * 85px);
}
</style>
