<script>
import FadeIn from "@/components/FadeIn.vue";
import simplebar from "simplebar-vue";
import sepBar from "@/components/sepBar.vue";
import "simplebar/dist/simplebar.min.css";

export default {
  name: "About",
  props: ["value"],
  components: {
    FadeIn,
    simplebar,
    sepBar
  },
  computed: {
    age() {
      return (
        new Date(Date.now() - Date.parse("Nov 15, 2001")).getFullYear() - 1970
      );
    },
    codeyrs() {
      return (
        new Date(Date.now() - Date.parse("August 1, 2013")).getFullYear() - 1970
      );
    }
  },
  methods: {
    scroll(tag) {
      let el = this.$refs[tag];
    },
    goHome() {
      document.querySelector(".simplebar-wrapper").classList.add("fadeOut");
      setTimeout(() => {
        this.$router.push("/");
      }, 450);
    },
    goTo(link) {
      window.open(link);
    }
  },
  mounted() {
    document.querySelector("#background").style.filter = "contrast(2)";
  }
};
</script>

