<template>
  <canvas ref="can"></canvas>
</template>

<script>
const g = 39.5;
const dt = 0.008; // 0.008 years is equal to 2.92 days
const dpi = 2;
const softeningConstant = 0.15;

/**
 * @type {CanvasRenderingContext2D}
 */
let ctx;
let canvas;
let masses = [];
let enable = true;

function updateMasses() {
  // Update Positions
  for (let i = 0; i < masses.length; i++) {
    masses[i].x += masses[i].vx * dt;
    masses[i].y += masses[i].vy * dt;

    if (masses[i].x < 0) masses[i].x = canvas.width - 1;
    if (masses[i].x > canvas.width) masses[i].x = 0;
    if (masses[i].y < 0) masses[i].y = canvas.height - 1;
    if (masses[i].y > canvas.height) masses[i].y = 0;
  }
}

function renderLoop() {
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  updateMasses();
  ctx.strokeStyle = "#EEEEEC";
  ctx.fillStyle = "#EEEEEC";
  for (let i = 0; i < masses.length; i++) {
    ctx.beginPath();
    ctx.ellipse(masses[i].x, masses[i].y, 6, 6, 0, 0, 2 * Math.PI);
    ctx.fill();
    ctx.stroke();

    for (let j = 0; j < masses.length; j++) {
      if (j == i) continue;

      const dx = masses[i].x - masses[j].x;
      const dy = masses[i].y - masses[j].y;
      const dist = Math.sqrt(dx * dx + dy * dy);
      if (dist < 255) {
        ctx.strokeStyle = `rgb(${48 + 207 * ((255 - dist) / 255)},${10 +
          245 * ((255 - dist) / 255)},${36 + 219 * ((255 - dist) / 255)})`;
        ctx.beginPath();
        ctx.moveTo(masses[i].x, masses[i].y);
        ctx.lineTo(masses[j].x, masses[j].y);
        ctx.stroke();
      }
    }
  }
  if (enable) requestAnimationFrame(renderLoop);
}

// FOUND FROM https://medium.com/wdstack/fixing-html5-2d-canvas-blur-8ebe27db07da
function fix_dpi() {
  // get CSS height
  // the + prefix casts it to an integer
  // the slice method gets rid of "px"
  const style_height = +getComputedStyle(canvas)
    .getPropertyValue("height")
    .slice(0, -2);
  // get CSS width
  const style_width = +getComputedStyle(canvas)
    .getPropertyValue("width")
    .slice(0, -2);
  // scale the canvas
  canvas.setAttribute("height", style_height * dpi);
  canvas.setAttribute("width", style_width * dpi);
  masses = [];
  for (let i = 0; i < (75 * canvas.width) / 1920; i++) {
    masses.push({
      m: Math.random() * 500,
      x: Math.random() * canvas.width,
      y: Math.random() * canvas.height,
      vx: Math.random() * 200 * (Math.random() - 0.5),
      vy: Math.random() * 200 * (Math.random() - 0.5)
    });
  }
}

window.addEventListener("resize", () => fix_dpi());

export default {
  name: "NetworkBack",
  props: ["enable"],
  mounted() {
    enable = this.enable;
    canvas = this.$refs.can;
    ctx = canvas.getContext("2d");
    fix_dpi();

    masses = [];
    for (let i = 0; i < (75 * canvas.width) / 1920; i++) {
      masses.push({
        m: Math.random() * 500,
        x: Math.random() * canvas.width,
        y: Math.random() * canvas.height,
        vx: Math.random() * 200 * (Math.random() - 0.5),
        vy: Math.random() * 200 * (Math.random() - 0.5)
      });
    }
    renderLoop();
  },
  watch: {
    enable(oVal, nVal) {
      enable = this.enable;
      renderLoop();
    }
  }
};
</script>

<style lang="scss" scoped>
canvas {
  width: 100%;
  height: 100%;
  background: #300a24;
}
</style>
