<template>
  <div id="app">
    <NetworkBack id="background" :enable="enableBack"></NetworkBack>
    <div class="enableBack">
      <input type="checkbox" id="backcheck" v-model="enableBack" />
      <label for="backcheck">Enable Background</label>
    </div>
    <router-view :firstOpen="loadedOnce" v-cloak />
  </div>
</template>

<script>
import NetworkBack from "@/components/NetworkBack.vue";

export default {
  components: { NetworkBack },
  data() {
    return {
      loadedOnce: 1,
      enableBack: true
    };
  },
  mounted() {
    this.loadedOnce = 0;
  }
};
</script>

<style lang="scss">
#app {
  font-family: "Source Code Pro", monospace;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.enableBack {
  z-index: 101;
  position: fixed;
  bottom: 0;
  right: 0;
  background: white;
  border-radius: 4px;
  pointer-events: all;
  margin: 1%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  padding: 0.3rem;
  & input {
    vertical-align: bottom;
  }
}
</style>
